* {
  font-family: "Lato", sans-serif;
}

#content {
  width: 100%;
  height: 100%;
  padding-right: 200px;
}

#menu li {
  display: inline;
}

#menu a:link,
#menu a:visited {
  text-decoration: none;
  color: #f2977d;
  font-weight: 300;
  font-size: 24px;
}

#menu :not(:last-child) {
  border-right: 2.5px solid #f2977d;
  padding-right: 15px;
  padding-left: 15px;
}

#menu li:last-child {
  padding-left: 15px;
  padding-right: 15px;
}

#container-logo {
  width: 139px;
}

#container-nav {
  width: content;
}

// #container-nav {
//   right: 10px;
//   position: absolute;
//   right: 200px;
// }

header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#content {
  padding-left: 150px;
}

.gros-titre {
  font-size: 40px;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}

#titre > p {
  text-align: center;
  font-size: 20px;
}

#images {
  overflow: auto;
  display: flex;
  width: 100%;
  gap: 10px;
  margin-top: 50px;
  margin-bottom: 50px;
  justify-content: center;
}

#texte-prestation {
  text-align: center;
  font-size: 16px;
}

.btn-reserver {
  width: 138px;
  height: 44px;
  border-radius: 20px;
  background-color: #f2977d;
  border: transparent;
}

.btn-reserver > span {
  padding: 10px;
  color: white;
  font-weight: 400;
  font-size: 24px;
}

#inscrire {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 30px;
}

#details {
  display: flex;
  align-items: justify;
  flex-direction: column;
  margin-top: 25px;
  margin-left: 20%;
  margin-right: 20%;
  line-height: 40px;
  border-radius: 41px;
  padding-left: 50px;
  padding-right: 50px;
  background-color: rgba(22, 22, 22, 0.05);
}

#details {
  background-color: rgba(22, 22, 22, 0.05);
}

#part-deux {
  display: flex;
  margin-top: -20px;
  margin-left: 10%;
}

#prestation-titre {
  margin-top: -70px;
}

@media all and (max-width: 990px) {
  #content {
    width: 100%;
    height: 100%;
    padding-left: 0%;
    padding-right: 0%;
  }
  #prestation-titre {
    margin-top: 0%;
  }
  #images a {
    width: 50%;
  }
  #details {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
    margin-right: 4%;
    margin-left: 0%;
    line-height: 25px;
    border-radius: 41px;
    padding-left: 50px;
    padding-right: 50px;
  }

  header {
    margin-bottom: 200px;
  }
}
